.product  {
    padding-top: 65px;
    padding-bottom: 98px;
   
@media screen and (min-width: 768px) {
    padding-top: 35px;
    padding-bottom: 112px;
}
@media screen and (min-width: 1200px) {
    padding-top: 40px;
    padding-bottom: 119px;

}
    & .container{
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;

    width: 320px;
    margin: 0 auto;

@media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 34px;
    padding-right: 34px;
}
@media screen and (min-width: 1200px) {
    width: 1200px;
    padding-left: 75px;
    padding-right: 75px;
}
  }
}
.product-list {
    margin-top: 144px;
    width: 100%;
    
    & .ice {
        background-color: $product1-color;
    }
    & .coffee {
        background-color: $product2-color;
    }
    & .shake {
        background-color: $product3-color;
    }
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: center;
         margin-top: 144px;
    }
@media screen and (min-width: 1200px) {
    margin-top: 259px;
}
}
.product-item {
    position: relative;
    width: 100%;
    padding-bottom: 40px;
    border: none;
    border-radius: 24px;
    &:not(:first-child) {
        margin-top: 130px;
    }
    @media screen and (min-width: 768px) {
        width: calc((100% - 40px)/3);
      &:not(:first-child) {
        margin-top: 0px;
    }
    &:not(:last-child) {
        margin-right: 20px;
    }
    }
     @media screen and (min-width: 1200px) {
        width: calc((100% - 60px)/3);
      &:not(:first-child) {
        margin-top: 0px;
    }
    &:not(:last-child) {
        margin-right: 30px;
    }
    }
}
.product-img {
    position: absolute;

    top: -113px;
    right: 5%;
    max-width: 249px;

     @media screen and (min-width: 768px) {
         max-width: 220px;
       top: -102px;
      right: -1%;
      }
       @media screen and (min-width: 1200px) {
         max-width: 335px;
       top: -210px;
      right: -1%;
      }
}
.product-name {
   padding-top: 161px;
font-family: $title-font;
font-weight: normal;
font-size: 20px;
line-height: 1.15;
letter-spacing: 0.04em;
text-transform: uppercase;
margin-top: 0;
margin-bottom: 30px;
 @media screen and (min-width: 768px) {
     padding-top: 152px;
 }
 @media screen and (min-width: 1200px) {
     padding-top: 163px;
     font-size: 30px;
line-height: 1.13;
margin-bottom: 32px;
 }
}
.product-points {
    display: inline-block;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: $white-title-color;
}
.product-text {
width: 100%;
font-weight: bold;
font-size: 14px;
line-height: 1.52;
margin-top: 30px;
margin-bottom: 30px;
padding-right: 40px;
padding-left: 40px;
letter-spacing: 0.04em;
@media screen and (min-width: 768px) {
     padding-left: 11px;
     padding-right: 11px;
     margin-bottom: 40px;
     font-size: 12px;
 }
 @media screen and (min-width: 1200px) {
     padding-left: 44px;
     padding-right: 44px;
     margin-top: 32px;
     margin-bottom: 60px;
     font-size: 16px;
line-height: 1.76;
 }
}
.product-link {
    display: inline-block;
width: 40px;
    height: 40px;
}
.product-arrow {
    width: 100%;
    border-radius: 50%;
}
