/* Загальні стилі */
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.page {
  font-family: $main-font;
  background-color: $main-background-color;
  color: $main-text-color;
  font-weight: 500;
  font-size: 12px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}
.list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.link {
  text-decoration: none;
}
.img {
  display: block;
  max-width: 100%;
  height: auto;
}
